<template>
  <v-ons-page>
    <custom-toolbar v-bind="toolbarInfo"></custom-toolbar>

    <v-ons-carousel fullscreen swipeable auto-scroll overscrollable
      :index.sync="carouselIndex"
    >
      <v-ons-carousel-item v-for="(item, key) in items" :key="key"
        class="carousel-item"
        :style="{ backgroundColor: item.color }"
      >
      <span v-html="item.page"></span>
      </v-ons-carousel-item>
    </v-ons-carousel>

    <div class="dots">
      <span v-for="dotIndex in Object.keys(items).length" :key="dotIndex"
        @click="carouselIndex = dotIndex - 1"
      >
        {{ carouselIndex === dotIndex - 1 ? '\u25CF' : '\u25CB' }}
      </span>
    </div>

  </v-ons-page>
</template>

<script>

export default {
  data () {
    return {
      carouselIndex: 0,
      items: [
        {
          page: `<div>
                  <h1>Employment History</h1>
                  <hr>
                  <h2>GIGABYTE</h2>
                  <h3>Software Engineer</h3>
                  <ul>
                    <li>To maintain and develop the enterprise management system with Java and JavaScript.</li>
                    <li>Take responsibilities for the requests from users.</li>
                    <li>Upgrade and refactor the enterprise management system in better pattern.</li>
                    <li>Play an important role as a programmer in our team.</li>
                  </ul>
                  <h2>Cruise Whitsundays</h2>
                  <h3>Reservation Consultant</h3>
                  <ul>
                    <li>Processes reservations by mail, telephone and from the sales office, hotel departments, and travel agents.</li>
                    <li>Operate resource at front desk office.</li>
                    <li>Upgrade and refactor the enterprise management system in better pattern.</li>
                    <li>Play an important role as a programmer in our team.</li>
                  </ul>
                  <h2>Tainan City Government Fire Bureau</h2>
                  <h3>Paramedics</h3>
                  <ul>
                    <li>Response to all ambulance calls</li>
                    <li>Work corporately and communicate effectively</li>
                    <li>Maintain high quality under stressful situation</li>
                  </ul>
                </div>`,
          color: 'grey'
        },
        {
          page: `<div class="card">
                  <h1>Education</h1>
                  <h2>Chung Yuan Christian University</h2>
                  <h3>Electronic Engineering</h3>
                  <ul>
                    <li>Special Topic on 4-bits Analog-to-digital converter</li>
                    <li>Club leader in Fortune-Telling Club</li>
                    <li>Training course in Institute for Information Industry</li>
                  </ul>
                </div>`,
          color: '#085078'
        },
        {
          page: `<div class="card">
                  <h1>Education</h1>
                  <h2>Chung Yuan Christian University</h2>
                  <h3>Electronic Engineering</h3>
                  <ul>
                    <li>Special Topic on 4-bits Analog-to-digital converter</li>
                    <li>Club leader in Fortune-Telling Club</li>
                    <li>Training course in Institute for Information Industry</li>
                  </ul>
                </div>`,
          color: '#373B44'
        }
      ]
    }
  }
}
</script>

<style scoped>
.carousel-item {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.color-tag {
  color: #fff;
  font-size: 48px;
  font-weight: bold;
  text-transform: uppercase;
}

.dots {
  text-align: center;
  font-size: 30px;
  color: #fff;
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
}

.dots > span {
  cursor: pointer;
}

.card {
	display: inline-block;
	width: 100%;
	padding: 1em;
}
</style>



