<template>
  <v-ons-page>
    <div class="wrapper-portfolio">
      <v-ons-card v-for="portfolio in portfolios" :key="portfolio.title">
        <img :src="portfolio.imageUrl" alt="Onsen UI" style="width: 100%">
        <div class="title">
          {{portfolio.title}}
        </div>
        <div class="content">
          <div>
            <v-ons-button><v-ons-icon icon="ion-thumbsup"></v-ons-icon></v-ons-button>
            <v-ons-button><v-ons-icon icon="ion-share"></v-ons-icon></v-ons-button>
          </div>
          <v-ons-list>
            <v-ons-list-header>Bindings</v-ons-list-header>
            <v-ons-list-item>{{portfolio.content1}}</v-ons-list-item>
            <v-ons-list-item>{{portfolio.content2}}</v-ons-list-item>
            <v-ons-list-item>React</v-ons-list-item>
          </v-ons-list>
        </div>
      </v-ons-card>
    </div>
    <v-ons-speed-dial position="bottom right" direction="up"
      :open.sync="spdOpen"
    >
      <v-ons-fab>
        <v-ons-icon icon="md-face"></v-ons-icon>
      </v-ons-fab>

      <v-ons-speed-dial-item v-for="(icon, name) in shareItems"
        @click="$ons.notification.confirm(`Share on ${name}?`)"
				:key="name"
      >
        <v-ons-icon :icon="icon"></v-ons-icon>
      </v-ons-speed-dial-item>

    </v-ons-speed-dial>
  </v-ons-page>
</template>

<script>
export default {
  data () {
    return {
      portfolios: {
        part1: {
          imageUrl: 'https://monaca.io/img/logos/download_image_onsenui_01.png',
          title: 'title1',
          content1: 'spinner',
          content2: 'red'
        },
        part2: {
          imageUrl: 'https://monaca.io/img/logos/download_image_onsenui_01.png',
          title: 'title2',
          content1: 'progress',
          content2: 'blue'
        }
      },
      spdOpen: false,
      shareItems: {
        'Twitter': 'md-twitter',
        'Facebook': 'md-facebook',
        'Google+': 'md-google-plus'
      }
    }
  },

  methods: {
    push (page, key) {
      this.$store.commit('navigator/push', {
        extends: page,
        data () {
          return {
            toolbarInfo: {
              backLabel: 'About',
              title: key
            }
          }
        }
      })
    }
  }
}
</script>

<style>

ons-card {
  cursor: pointer;
  color: #333;
}

.card__title, .card--material__title {
  font-size: 20px;
}

 .wrapper-portfolio {
	display: grid;
	grid-gap: 10px;
	grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
	padding-right: 4%;
	padding-left: 3%;
}

.box {
	border: #444;
	border-radius: 5px;
	padding: 20px;
	font-size: 150%;
}

.card {
	display: inline-block;
	width: 100%;
}

.summary {
	margin: 5%;
}
</style>



