<template>
  <v-ons-page>
		<div class="about-intro">
			<h1>About Me</h1>
			<h2>I'm Jenan Hong, a front-end developer and MIS from Taiwan.</h2>
			<h3>I currently work at GIGABYTE in Taiwan, focusing on enterprise information system.
			I also enjoy solving complex problems and making efficient code.
			<br>
			Recently, I am focusing on Vue.js, OnsenUI and PWA to develop my skills
			Contact me if you have great ideas to spread by websites. :D</h3>
		</div>

		<!-- <div class="wrapper-about">
			<div 
			class="card"
			v-for="page of pages" 
      :key="page.label"
      @click="push(page.component, page.label)">
				<div class="title">{{ page.label }}</div>
      	<div class="content">{{ page.h2 }}</div>
				<div class="content">{{ page.h3 }}</div>
			</div>
    </div> -->
    <div class="list-info">
      <v-ons-list modifier="inset">
        <v-ons-list-header>Experience</v-ons-list-header>
        <v-ons-list-item v-for="Experience of Experiences" 
          :key="Experience.title"
          @click="push(Experience.component, Experience.title)"
          modifier="chevron">
          <div class="left">
            <v-ons-icon icon="md-face"></v-ons-icon>
          </div>
          <div class="">
            <div class="title">{{ Experience.title }}</div>
            <div class="content">{{ Experience.company }}</div>
          </div>
        </v-ons-list-item>


        <v-ons-list-header>Education</v-ons-list-header>
        <v-ons-list-item v-for="Education of Educations" 
          :key="Education.label"
          @click="push(Education.component, Education.label)"
          modifier="chevron">
          <div class="left">
            <v-ons-icon icon="md-face"></v-ons-icon>
          </div>
          <div class="">
            <div class="title">{{ Education.school }}</div>
            <div class="content">{{ Education.department }}</div>
          </div>
        </v-ons-list-item>

        <v-ons-list-header>Skill</v-ons-list-header>
        <v-ons-list-item v-for="Skill of Skills" 
          :key="Skill.title"
          @click="push(Skill.component, Skill.title)"
          modifier="chevron">
          <div class="left">
            <v-ons-icon icon="md-face"></v-ons-icon>
          </div>
          <div class="">
            <div class="content">
              <div class="">
                {{ Skill.skill }}
              </div>
            </div>
          </div>
        </v-ons-list-item>
      </v-ons-list>
    </div>
    <br/>

		<v-ons-speed-dial position="bottom right" direction="up"
      :open.sync="spdOpen"
    >
      <v-ons-fab>
        <v-ons-icon icon="md-face"></v-ons-icon>
      </v-ons-fab>

      <v-ons-speed-dial-item v-for="(icon, name) in shareItems"
        @click="$ons.notification.confirm(`Share on ${name}?`)"
				:key="name"
      >
        <v-ons-icon :icon="icon"></v-ons-icon>
      </v-ons-speed-dial-item>

    </v-ons-speed-dial>
  </v-ons-page>
</template>

<script>
import Carousel from './Carousel.vue'

export default {
  data () {
    return {
      Experiences: [
        {
          component: Carousel,
          title: 'Software Engineer',
          company: 'GIGABYTE'
        },
        {
          component: Carousel,
          title: 'Reservation Consultant',
          company: 'CRUISE WHITSUNDAYS PTY LTD'
        },
        {
          component: Carousel,
          title: 'Housekeeper & Barista',
          company: 'Whitsunday Apartments Hamilton Island'
        },
        {
          component: Carousel,
          title: 'Paramedics',
          company: 'Tainan City Government Fire Bureau'
        }
      ],

      Educations: [
        {
          component: Carousel,
          school: 'Chung Yuan Christian University',
          department: 'Electronic Engineering'
        }
      ],

      Skills: [
        {
          component: Carousel,
          title: 'Skills',
          skill: 'Javscript, Java, Vue.js, OOP, Git, SQL'
        }
      ]
    }
  },

  methods: {
    push (page, key) {
      this.$store.commit('navigator/push', {
        extends: page,
        data () {
          return {
            toolbarInfo: {
              backLabel: 'About',
              title: key
            }
          }
        }
      })
    }
  }
}
</script>

<style>
.intro {
  text-align: left;
  padding: 0 22px;
  margin-top: 20px;
  font-size: 14px;
  line-height: 1.4;
  color: rgba(0, 0, 0, .54);
}

ons-card {
  cursor: pointer;
  color: #333;
}

.card__title, .card--material__title {
  font-size: 20px;
}

.bg-dark {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.4);
  background-size: cover;
  z-index: -900;
}

 .wrapper-about {
	display: grid;
	grid-gap: 10px;
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
	padding-right: 4%;
	padding-left: 3%;
}

.box {
	background-color: lightblue;
	border: #444;
	border-radius: 5px;
	padding: 20px;
	font-size: 150%;
}

.card {
	display: inline-block;
	width: 100%;
}

.div-cards {
	padding-right: 4%;
	padding-left: 3%;
}

.about-intro {
	margin-right: 5%;
  margin-left: 5%;
}

.list-info {
  padding-right: 4.5%;
	padding-left: 3.5%;
}
</style>



