<template>
  <v-ons-page>
    <div class="bg-dark"></div>
    <div class="bg-img"></div>
    <div class="align-middle">
      <h1>Jenan Hong</h1>
      <h1>I am front-end developer.</h1>
      <h3>An interactive Web Designer with the passion for creativity.</h3>
    </div>
  </v-ons-page>
</template>

<script>
import Carousel from './Carousel.vue'

export default {
  data () {
    return {
      pages: [
        {
          component: Carousel,
          label: 'Carousel',
          desc: 'Customizable carousel that can be optionally fullscreen.'
        }
      ]
    }
  },

  methods: {
    push (page, key) {
      this.$store.commit('navigator/push', {
        extends: page,
        data () {
          return {
            toolbarInfo: {
              backLabel: 'Home',
              title: key
            }
          }
        }
      })
    }
  }
}
</script>

<style>
.intro {
  text-align: left;
  padding: 0 22px;
  margin-top: 20px;
  font-size: 14px;
  line-height: 1.4;
  color: rgba(0, 0, 0, .54);
}

ons-card {
  cursor: pointer;
  color: #333;
}

.card__title, .card--material__title {
  font-size: 20px;
}

.bg-img {
  position:fixed;
  width: 100vw;
  height: 100vh;
  background: url('https://i.pinimg.com/564x/ec/31/e2/ec31e2094c6398590104a6576dccc0b6.jpg') center center no-repeat;
  background-size: cover;
  z-index: -1000;
}

.bg-dark {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.4);
  background-size: cover;
  z-index: -900;
}

.align-middle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 85vh; 
  /* display: table-cell;
  vertical-align: middle;
  text-align: center;
  width: 100vw;
  height: 100vh; */
  /* border-radius: 1em;
  padding: 1em;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%) */
  text-align: center;
  color: white;
  z-index: 10;
}
</style>



