<template>
  <v-ons-page>
		<div class="wrapper-contact">
			<div class="box-contact">
				<h1>Contact Me</h1>
				<h2>Do you need a great partner for your busniness idea? Check me out below. Feel free to contact me either in English or Chinese. :D</h2>
        <div>
          <v-ons-fab class="marginB">
            <v-ons-icon icon="md-linkedin"></v-ons-icon>
          </v-ons-fab>
          Like me on Linkedin
        </div>
        <div>
          <v-ons-fab class="marginB">
            <v-ons-icon icon="md-facebook"></v-ons-icon>
          </v-ons-fab>
          Like me on facebook
        </div>
        <div>
          <v-ons-fab class="marginB">
            <v-ons-icon icon="md-twitter"></v-ons-icon>
          </v-ons-fab>
          Like me on twitter
        </div>
			</div>
			<div class="box-contact">Put a great picture about your self here.</div>
      
		</div>
    <div class="form-email" id="id-form">
      <v-ons-list modifier="inset">
        <v-ons-list-header>Your name</v-ons-list-header>
        <v-ons-list-item modifier="material">
          <v-ons-input float 
            placeholder="Name"
            modifier="material"
            type="text"
            :size="inputLength"
          >
          </v-ons-input>
        </v-ons-list-item>
        <v-ons-list-header>Your email</v-ons-list-header>
        <v-ons-list-item modifier="material">
          <v-ons-input float 
            placeholder="Email"
            modifier="material"
            type="text"
            :size="inputLength"
          >
          </v-ons-input>
        </v-ons-list-item>
        <v-ons-list-header>Your Message</v-ons-list-header>
        <v-ons-list-item modifier="material">
          <textarea class="textarea textarea--transparent" 
          rows="3" 
          placeholder="Message"></textarea>
        </v-ons-list-item>
      </v-ons-list>
    </div>
  </v-ons-page>
</template>

<script>
export default {
  data () {
    return {
      spdOpen: false,
      shareItems: {
        'Twitter': 'md-twitter',
        'Facebook': 'md-facebook',
        'Google+': 'md-google-plus'
      },
      inputLength: document.getElementById('id-form').offsetWidth / 9,
      textareaLength: document.getElementById('id-form').offsetWidth / 9
    }
  }
}
</script>

<style>

ons-card {
  cursor: pointer;
  color: #333;
}

.card__title, .card--material__title {
  font-size: 20px;
}
@media (min-width: 500px) {
    .wrapper-contact {
      display: grid;
      grid-template-columns: minmax(400px, auto);
      padding-right: 4%;
      padding-left: 3%;
    }
}
@media (min-width: 700px) {
    .wrapper-contact {
      display: grid;
	    grid-gap: 10px;
	    grid-template-columns: 1fr 2fr;
	    padding-right: 4%;
	    padding-left: 3%;
    }
}

.form-email {
  padding-right: 3.5%;
	padding-left: 2.5%;
}

.box-contact {
	border: #444;
	border-radius: 5px;
	padding: 20px;
	font-size: 150%;
}

.card {
	display: inline-block;
	width: 100%;
}

.marginB {
  margin-bottom: 0.5em;
}
</style>



